@import url('https://fonts.googleapis.com/css?family=Cute+Font');

/* universal styling */
h1 {
    font-family: 'Cute Font', cursive;
    font-size: 4.3vw;
}

.header {
    margin-bottom: 3.5%;
}

/* ABOUT SECTION */

/* about container */
#about {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 5% 0% 20% 0%;
    border-bottom: 1px solid lightgrey;
}

#description-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

#pic {
    width: 40%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding-right: 5%;
}

#pic > img {
    border-radius: 50%;
    height: 100%;
    width: 70%;
} 

#border {
    width: 5%;
    border-left: 4px solid #10828C;
}

#description {
    width: 45%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

#description > :first-child {
    font-size: 3vw;
}

#description > :last-child {
    line-height: 2;
    color: #888;
    font-size: 1.2vw;
}



